.header {
  height: 100vh;

  background-image: url("../images/header_image.png");
  background-size: cover;
  background-position: center;

  &__logo {
    background-color: url("../images/THE_MET.png");
  }

  &__phone {
    display: block;
    width: 18px;
    height: 18px;

    background-image: url("../images/phone.svg");

  }

  &__menu {
    display: block;
    width: 18px;
    height: 12px;

    background-color: url("../images/menu.svg");

    &:hover {
      background-image: url("../images/menu__hover.svg");
    }
  }
}
